* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: $background-light;
  font-family: 'Lato', sans-serif;
  font-weight: 200;
  color: $font-dark;
}
#wrapper {
  overflow-x: hidden;
  position: relative;
  min-height: 100vh;
}

h1 {
  font-weight: 300;
  font-size: 4em;
}

a h1 {
  display: inline-block;
}

h2 {
  font-weight: 300;
  font-size: 3em;
}

h3 {
  font-weight: inherit;
  font-size: 2em;
}

h4 {
  font-weight: 500;
}

a {
  color: $font-dark;
}

nav {
  background-color: $background-light-secondary;
  #socials {
    position: absolute;
    top: 0px;
    right: 0px;
  }
}

#socials,
#nav-links {
  a {
    display: inline-block;
    padding: 10px;
  }
  :hover {
    background-color: grey;
  }
}

#socials {
  text-align: center;
}

section {
  padding-left: 10%;
  padding-right: 10%;
  margin-bottom: 70px;
}

.section-header {
  display: block;
}

.section-body {
  display: grid;
  justify-items: center;
  padding: 6px;
}

article {
  background-color: $background-light-secondary;
  position: relative;
  width: 500px;
  height: 250px;
  margin: 6px;
  padding: 30px;
  border: solid;
  border-radius: 20px;
  // background-color: red;
}

/*
* About 
*/

#about-blurb {
  width: 100%;
  padding-left: 0%;
  padding-right: 0%;

  article {
    text-align: center;
    padding-left: 20%;
    padding-right: 20%;

    width: 100%;
    border: none;
    border-radius: 0px;
    height: auto;
  }
}

/* 
  * Header 
  */

header {
  display: grid;
  justify-items: center;
}

#home-title div:nth-child(1) {
  display: inline-block;
  float: left;
  padding-top: 25px;
  padding-bottom: 25px;
  height: 200px;
}

#home-title div h1:nth-child(1) {
  letter-spacing: 0.021em;
}

#home-title div:nth-child(2) {
  display: inline-block;
  padding-left: 30px;
  img {
    border: solid;
    border-radius: 50%;
    width: 125px;
    margin-top: 45px;
  }
}

// Blog post

#blog-post {
  height: auto;
}

// Clouds
.cloud {
  background: $background-light-secondary;
  position: absolute;
  z-index: -1;
  height: 50px;
  width: 130px;
  margin-top: 100px;
  box-shadow: 10px 10px rgba(0, 0, 0, 0.2);
  border-radius: 100px;
}

.cloud::before,
.cloud::after {
  content: '';
  background: inherit;
  display: inline-block;
  position: relative;
  border-radius: inherit;
}

.cloud::before {
  width: 60px;
  height: 60px;
  top: -30px;
  left: 40px;
}
.cloud::after {
  width: 30px;
  height: 30px;
  top: -50px;
  left: -40px;
}

// Cloud specific positioning.
#cloud1 {
  top: 20px;
  left: 10%;
}

#cloud2 {
  top: 70px;
  left: 80%;
}

#cloud3 {
  top: 205px;
  left: 15%;
}

#cloud4 {
  top: 0px;
  right: 25%;
}

/*
* Footer
*/

footer {
  display: grid;
  justify-items: center;
  background-color: $background-light-secondary;
  margin-top: 30px;

  position: absolute;
  bottom: 0;
  width: 100%;
  height: 2.5rem;
}

/*
Media queries
*/
@media (max-width: 800px) {
  section {
    padding-left: 3%;
    padding-right: 3%;
  }
}

@media (max-width: 550px) {
  article {
    width: 450px;
  }

  .project-links {
    padding: 0 10px 10px;
  }

  #home-title div:nth-child(2) {
    padding-left: 10px;
  }
}

@media (max-width: 400px) {
  article {
    width: 100%;
  }

  h1 {
    font-size: 3em;
  }

  h2 {
    font-size: 2em;
  }

  h3 {
    font-size: 1.5em;
  }

  #home-title div:nth-child(2) {
    img {
      width: 100px;
      margin-top: 35px;
    }
  }
}

/* smartphones, touchscreens */
@media (hover: none) and (pointer: coarse) {
  #home-title div h1:nth-child(1) {
    letter-spacing: 0.05em;
  }
}
